import '../css/home.css'
import Header from '../components/Header'
import Footer from '../components/Footer'
import React from "react";

const ReactDOMServer = require('react-dom/server');
const HtmlToReactParser = require('html-to-react').Parser;

const htmlInput = ' <h1 class="qui">Qui somme nous ?</h1>\n' +
    '    <div class="somme">\n' +
    '    <img src="questions.png" alt="batiment cole epsi" srcset="" class="imgbatepsi">\n' +
    '    <p class="paragraphe">Bienvenue dans votre cole de cyberscurit  Paris,\n' +
    '         le choix idal si vous aspirez  exceller dans le domaine\n' +
    '         de la cyberscurit au cur de la capitale franaise ! \n' +
    '         Paris, reconnue pour son effervescence technologique et \n' +
    '         son innovation constante, offre un cadre exceptionnel pour\n' +
    '        vous plonger dans les complexits de la scurit informatique. \n' +
    '        Nous vous prparons  affronter les dfis de la cyberscurit avec une formation rigoureuse,\n' +
    '        intgrant thorie et pratique. Profitez pour cela dun rseau tendu de partenaires technologiques et de start-ups,\n' +
    '         enrichissant votre parcours avec des expriences relles et des opportunits de carrire dans une ville qui est  la pointe de la technologie mondiale.</p>\n' +
    '    </div> \n' +
    '\n' +
    '    <h1 class="qui">Objectifs de la platforme MAPSI</h1>\n' +
    '    <div class="map">\n' +
    '        <div class="contmapun"><img class="localisation" src="localisation.png" alt=""><div class="mapun">Localisation des tages et salles de classe</div></div>\n' +
    '        <div class="contmapdeux"><img class="acces" src="accs.png" alt=""><div class="mapdeux">Accs aux services et installation</div></div>\n' +
    '        <div class="contmaptrois"><img class="guide" src="Guide.png" alt=""><div class="maptrois">Guidage des nouveaux tudiants et visiteurs</div></div>\n' +
    '    </div>\n' +
    '    <h1 class="qui">Activits et Evnements</h1>\n' +
    '    <div class="container">\n' +
    '        <div class="contun">\n' +
    '        <div class="containerun"><img class="defense" src="defense.png" alt=""><div class="texteun">Un quartier dynamique</div></div>\n' +
    '        <div class="containerdeux"><img class="salle" src="salleconf.png" alt=""><div class="textedeux">Confrences organises et invitations d\'alummnis</div></div>\n' +
    '        </div>\n' +
    '        <div class="contdeux">\n' +
    '        <div class="containertrois"><img class="work" src="imgwork.png" alt=""><div class="textetrois">Semaine d\'intgration avec le workshop</div></div>\n' +
    '        <div class="containerquatre"><img class="asso" src="imgasso.png" alt=""><div class="textequatre">Vie associative au sein de l\'cole</div></div>\n' +
    '        </div>\n' +
    '    </div>';
const htmlToReactParser = new HtmlToReactParser();
const reactElement = htmlToReactParser.parse(htmlInput);
ReactDOMServer.renderToStaticMarkup(reactElement);

export default function Apropos() {
    return (
        <div className='home'>
            <Header />
            {reactElement}
            <Footer />
        </div>
    )
}
