import '../css/home.css'
import Header from '../components/Header'
import Footer from '../components/Footer'
import React from "react";


const ReactDOMServer = require('react-dom/server');
const HtmlToReactParser = require('html-to-react').Parser;

const htmlInput = '<div id="corps">\n' +
    '\n' +
    '    <div className="line1">\n' +
    '        <form action="/recherche" method="get">\n' +
    '            <input type="text" name="query" placeholder="Recherche..."/>\n' +
    '            <button type="submit">Rechercher</button>\n' +
    '        </form>\n' +
    '    </div>\n' +
    '\n' +
    '    <div className="line2">\n' +
    '        <ul>\n' +
    '            <li className="campus">\n' +
    '                <img src="campus_epsiParis.jpeg" alt=""/>\n' +
    '            </li>\n' +
    '            <li>\n' +
    '                <p className="textanime">Bienvenue dans votre cole de cyberscurit  Paris, le choix idal si vous\n' +
    '                    aspirez  exceller dans le domaine de la cyberscurit au cur de la capitale franaise ! Paris,\n' +
    '                    reconnue pour son effervescence technologique et son innovation constante, offre un cadre\n' +
    '                    exceptionnel pour vous plonger dans les complexits de la scurit informatique. Nous vous prparons\n' +
    '                     affronter les dfis de la cyberscurit avec une formation rigoureuse, intgrant thorie et\n' +
    '                    pratique. Profitez pour cela dun rseau tendu de partenaires technologiques et de start-ups,\n' +
    '                    enrichissant votre parcours avec des expriences relles et des opportunits de carrire dans une\n' +
    '                    ville qui est  la pointe de la technologie mondiale.</p>\n' +
    '            </li>\n' +
    '        </ul>\n' +
    '    </div>\n' +
    '\n' +
    '</div>';

const htmlToReactParser = new HtmlToReactParser();
const reactElement = htmlToReactParser.parse(htmlInput);
ReactDOMServer.renderToStaticMarkup(reactElement);

export default function Home() {
    return (
        <div className='home'>
            <Header/>
            {reactElement}
            <Footer/>
        </div>
    )
}

